.App {
  text-align: center;
}
html, body, #root{
  width: 100%;
  height: 100%;
}

.container{
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: auto;
}
.content{
    position: relative;
    margin: auto;
    /*background: #999;*/
}
.item{
  position: absolute;
  width: 200px;
  height: 320px;
  background: #dedede;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}


